import React, { memo } from 'react'
import { IconButton, Image } from "@chakra-ui/react";

import Box from '../components/Box';
import Flex from '../components/Flex';
import { responsive } from '../components/ThemeProvider/theme';

import logo from './logo.svg'
import { ReactComponent as Menu } from './menu.svg'

const Header = ({ active, ...props }) => (
  <Flex
    position="fixed"
    top={0}
    left={0}
    right={0}
    alignItems="center"
    justifyContent={responsive('flex-end', 'space-between')}
    zIndex="docked"
    px="2em"
    _before={{
      content: '""',
      position: 'absolute',
      display: 'block',
      left: 0,
      right: 0,
      top: 0,
      bottom: 0,
      bgImage: 'linear-gradient(180deg, #000000 0%, #5C5C5C 100%)',
      opacity: Number(active),
      transition: 'opacity 0.75s',
    }}
    {...props}
  >
    <Box
      position={responsive('absolute', 'relative')}
      left={responsive('50%', 'auto')}
      transform={responsive(`translate(-50%, ${active ? '0' : 'calc(100vh - 14em)'})`, 'none')}
      transition="all 1s"
      width={responsive(active ? '118px' : '138px', '138px')}
    >
      <Image src={logo}  />
    </Box>
    <IconButton
      variant="ghost"
      colorScheme="blackAlpha"
      icon={<Menu />}
    />
  </Flex>
)

export default memo(Header)
