import React, { createElement, useCallback, useRef, useState } from 'react'
import ReactFullpage from '@fullpage/react-fullpage';
import { range } from 'lodash';

import useResponsive from '../contexts/mediaQuery/useResponsive'

import Layout from './Layout'
import Container from '../components/Container';
import Box from '../components/Box';
import Page1 from './Pages/1'

import NextButton from './NextButton'

const pages = [
  { comp: 'div', bg: 'full', next: '進入報告' },
  { comp: 'div', bg: 'side', next: '總體產值' },
  { comp: require('./Pages/3/Loadable').default, bg: ['none', 'fade' ], next: '下一頁' },
  { comp: require('./Pages/4/Loadable').default, bg: 'none', next: '2020產值變化' },
  { comp: require('./Pages/5/Loadable').default, bg: ['side', 'side'], next: '下一頁' },
  { comp: require('./Pages/6/Loadable').default, bg: 'none', next: '下一頁' },
  { comp: require('./Pages/6/Loadable').default, bg: 'none', next: '下一頁', dataKey: 1 },
  { comp: require('./Pages/7/Loadable').default, bg: ['none', 'fade' ], next: '下一頁' },
  ...range(4).map(d => ({ comp: require('./Pages/8/Loadable').default, bg: 'none', next: '下一頁', dataKey: d })),
  { comp: require('./Pages/12/Loadable').default, bg: 'none', next: '藝FUN券振興效果' },
]

const HomePage = () => {
  const { isMobile } = useResponsive()
  const [currentPage, setCurrentPage] = useState(0)
  const fpApi = useRef()
  const makePages = useCallback(({ fullpageApi }) => {
    fpApi.current = fullpageApi
    return (
      <ReactFullpage.Wrapper>
        {pages.map((p, i) => (
          <section
            className="section"
            data-anchor={`page${i + 1}`}
            key={i}
          >
            <Container>
              {createElement(p.comp, {
                fullpageApi,
                isMobile,
                dataKey: p.dataKey
              })}
            </Container>
          </section>
        ))}
      </ReactFullpage.Wrapper>
    )
  }, [isMobile])
  return (
    <Layout isMobile={isMobile} active={currentPage > 0} bg={pages[currentPage]?.bg}>
      <ReactFullpage
        //fullpage options
        licenseKey={process.env.REACT_APP_FULLPAGE_KEY}
        scrollingSpeed={1000}
        // scrollOverflow
        // autoScrolling={isDesktop}
        // autoScrolling={false}
        onLeave={(origin, destination) => setCurrentPage(destination.index)}
        render={makePages}
      />
      <Box.FullAbs pointerEvents="none">
        <Container position="relative" height="100%">
          <Page1 currentPage={currentPage} fullpageApi={fpApi.current} isMobile={isMobile} />
        </Container>
      </Box.FullAbs>
      <NextButton opacity={Number(isMobile || currentPage > 0)} onClick={() => fpApi.current.moveSectionDown()}>
        {pages[currentPage]?.next}
      </NextButton>
    </Layout>
  )
}

export default HomePage
